import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import thrills from "./thrills";
import "./home.css";
import audio_file from "./bg-music.mpeg"


class Home extends Component {
  render() {
    return (
      <HashRouter>
        <div>
          <header class="video-header">
            <video src="./video.mp4" autoPlay loop muted></video>
          </header>

          <div>
            <audio id="bg-music" src={audio_file} autoPlay loop></audio>
          </div>
          <main>
            <div class="frame">
              {/* <h1>Adrenaline Junkie</h1> */}
              <div class="slideshow">
                <figure class="slide slide--current">
                  <figcaption class="slide__caption">
                    <h2 class="slides__caption-headline">
                      <span class="text-row"><span>DREAMSCAPE</span></span>
                      <span class="text-row"><span>A <i id="slant"><b>DESIRE</b></i> TO ESCAPE</span></span>
                    </h2>
                    <NavLink to="thrills"><a class="slides__caption-link" href="#"><span>Explore</span></a></NavLink>
                    <center><p class="copyright">  Copyright Â© 2021 Adrenaline Junkie | Designed and Developed by Binita and Rishabh</p></center>
                  </figcaption>
                </figure>
              </div>
            </div>
          </main>
          <div className="content">
            <Route path="/thrills" component={thrills} />
          </div>

        </div>
      </HashRouter >
    );
  }
}

export default Home;