import React, { Component } from "react";
import {
    Route,
    HashRouter,
    Redirect
} from "react-router-dom";
import Home from "./home";
import thrills from "./thrills"

class Main extends Component {
    render() {
        return (
            <HashRouter>
                <div>
                    <script>
                        <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta2/dist/css/bootstrap.min.css" rel="stylesheet"
                            integrity="sha384-BmbxuPwQa2lc/FVzBcNJ7UAyJxM6wuqIj61tLrc4wSX0szH/Ev+nYRRuWlolflfl" crossorigin="anonymous"></link>
                        <title>ADRENALINE JUNKIE</title>
                    </script>
                    <div className="content">
                        <Route exact path="/">
                            <Redirect to="/home" />
                        </Route>
                        <Route path="/home" component={Home} />
                        <Route path="/thrills" component={thrills} />
                    </div>
                </div>
            </HashRouter >
        );
    }
}

export default Main;