import React from "react";
import data from "./data";
import './thrill.css';

function thrills() {
    function KeyLog() {
        window.addEventListener('keypress', function (e) {
            if (e.key === 'Enter') {
                window.location.reload()
            }
        }
        );

        window.addEventListener('dblclick', function () {
            window.location.reload()
        }
        );
    }

    return (
        <div>
            <div class='rotatewarning'>
                <img style={{ width: "100vw", height: "95vh" }} src='./rotatewarning.png' alt='rotate'></img>
            </div>
            <center>
            <div class='thrillervideo' onLoad={KeyLog}>
                <iframe src={data[Math.floor(Math.random() * data.length)]} title="YouTube video player" frameborder="0"  allowfullscreen></iframe>
            </div>
            <div class='explorebtn'>
            <button onClick={() => {window.location.reload()}}><span>EXPLORE</span></button>
            </div>
            </center>
        </div>
    )
}

export default thrills;